</template>

<script>

export default {
    name: "Commands_Gernal",
    data() {
        return {
            hamburger: false,
            font_list: ["fas fa-home", "fas fa-pills", "fas fa-cog fa-spin", "fas fa-crosshairs", "fas fa-money-bill", "fas fa-grin-beam", "fas fa-chart-bar", "fas fa-images", "fas fa-list", "fas fa-database"],
            name_list: ["메인", "|일반 명령어", "서버 세팅 명령어", "서버 관리 명령어", "도박 명령어", "재미 명령어", "통계 명령어", "짤 명령어", "기타 명령어", "데이터 삭제"],
            link_list: ["/", "/gernal", "/setting", "/manage", "/gambling", "/fun", "/stat", "/meme", "/app", "/data"],
            command_name: ["구글검색", "디데이 추가", "디데이 삭제", "디데이 리스트", "랭크", "링크단축", "서버프로필", "스티커스틸", "유저정보", "유튜브검색", "이모지스틸", "이모지확대", "인증", "투표", "프사", "한타로", "앱", "코드업로드"],
            command_description: ["구글에 검색한 결과 10개를 불러옵니다", "디데이를 추가합니다", "디데이를 삭제합니다", "디데이 리스트를 확인합니다", "서버 채팅 경험치, 레벨을 보여줍니다", "링크를 단축합니다 (+QR코드)", "서버 프로필을 보여줍니다", "다른 서버에 있는 스티커를 서버에 추가합니다", "유저 정보를 보여줍니다", "유튜브 검색 결과를 보여줍니다", "다른 서버에 있는 이모지를 서버에 추가합니다", "이모지를 확대해서 보여줍니다", "캡챠 인증 시스템 인증 명령어입니다", "투표를 시작합니다", "유저의 프로필 사진을 확대해서 보여줍니다", "한국어 타자로 치려 했지만 실수로 영어 타자로 쳤다면 이 명령어를 사용해 보세요", "알로항의 앱을 사용할 때 메시지 내용을 공개/비공개로 표시할지 설정하는 명령어", "SourceBin에 코드를 업로드합니다",]
